import React from "react";
import styles from "./image_slide.module.css";
import Slider from "react-slick";
import styled from "styled-components";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

const ImageSlide = (props) => {
  let settings = {
    // dots: true,
    infinite: true,
    speed: 500,
    autoplay: true,
    autoplaySpeed: 5000,
    slidesToShow: 1,
    slidesToScroll: 1,
  };

  return (
    <>
      <Wrap>
        <Slider {...settings}>
          <div className={styles.banner}>
            <img src={process.env.PUBLIC_URL + "/imgs/banner.png"} alt="" />
          </div>
          <div className={styles.banner}>
            <img src={process.env.PUBLIC_URL + "/imgs/banner2.png"} alt="" />
          </div>
        </Slider>
      </Wrap>
    </>
  );
};
const Wrap = styled.div`
  .slick-prev {
    left: 10px;
    z-index: 1;
    opacity: 0.5;
  }

  .slick-next {
    right: 10px;
    z-index: 1;
    opacity: 0.5;
  }
`;
export default ImageSlide;
